module.exports = {
  state: {
    viewControlHeight: 20, // 试图控制层高度
    godMobileCameraSetting: { // 上帝视角的活动相机配置
      fov: 45,
      aspect: '',
      near: 1,
      far: 10000
    }
  },
  getters: {
    // 获取控制层高度
    getViewControlHeight (state) {
      return state.viewControlHeight
    }
  },
  mutations: {
    // 配置上帝视角活动相机
    setGodMobileCamera (state, setting) {
      state.fov = setting.fov || state.fov
      state.aspect = setting.aspect || state.aspect
      state.near = setting.near || state.near
      state.far = setting.far || state.far
    }
  },
  actions: {
  }
}
