<template>
  <div class="bottomNav-container">
    <operation-control :width="operationControlSetting.size"></operation-control>
    <track-control></track-control>
  </div>
</template>

<script>
import operationControl from './bottomNav/operationControl' // 操作部分
import trackControl from './bottomNav/trackControl' // 轨道部分
export default {
  components: {
    operationControl, // 操作层
    trackControl // 轨道层
  },
  data () {
    return {
      operationControlWidthBuffer: '', // 操作层缓存
      operationControlSetting: { // 操作控制层配置
        size: '',
        max: 1000,
        min: 200
      },
      boxWatch: '' // 监听缓存
    }
  },
  computed: {

  },
  created () {
    this.operationControlSetting.size = this.$store.getters.getOperationControlWidth
    this.operationControlWidthBuffer = this.operationControlSetting.size
  },
  watch: {
    // 监听$store标志变形
    '$store.state.dragBox.sign' (newVal, oldVal) {
      if (newVal) {
        if (newVal === 'operation') {
          this.boxWatch = this.$watch('$store.state.dragBox.mousemoveX', (x) => {
            let changeSize = this.operationControlWidthBuffer + x
            if (changeSize > this.operationControlSetting.max) {
              changeSize = this.operationControlSetting.max
            }
            if (changeSize < this.operationControlSetting.min) {
              changeSize = this.operationControlSetting.min
            }
            this.operationControlSetting.size = changeSize
          })
        }
      } else {
        this.boxWatch && this.boxWatch()
        if (oldVal === 'operation') {
          this.operationControlWidthBuffer = this.operationControlSetting.size
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.bottomNav-container {
  .boxSetting();
  .flexLayout();
}
</style>
