<template>
  <div class="topConfig-container">
    <el-dropdown
    trigger="click"
    v-for="item in config"
    :key="item.name"
    @command="handleCommand"
    placement="bottom-start">
      <span class="el-dropdown-link">
        <mmd-icon :code="item.icon"></mmd-icon><span v-text="item.desp"></span>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item
        v-for="operateItem in item.operate"
        :key="operateItem.name"
        :command="operateItem.run">
          <span class="operate-name" v-text="operateItem.desp"></span>
          <span class="key-combination" v-text="getKeyCombination(operateItem.shortcut)"></span>
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </div>
</template>

<script>
export default {
  computed: {
    // 配置数据
    config () {
      return this.$store.getters.getTopConfig
    }
  },
  methods: {
    // 获取组合键
    getKeyCombination (list) {
      if (list[0]) {
        let str = ''
        list.forEach(elem => {
          str += `${elem} + `
        })
        str = str.slice(0, -3)
        return str
      }
    },
    // 下拉点击方法
    handleCommand (run) {
      run && run()
    }
  }
}
</script>

<style lang="less" scoped>
.topConfig-container {
  .boxSetting();
  .flexLayout(row, flex-start, center);
  > .el-dropdown {
    margin: 0 @box-margin;
    > .el-dropdown-link {
      > .icon-container {
        color: @theme-color;
        margin: 0 5px;
      }
      .transitionSetting({
        color: @themeHover-color;
      });
    }
  }
}
.el-dropdown-menu {
  width: 180px;
  > .el-dropdown-menu__item {
    .flexLayout(row, space-between, center);
    > .operate-name {}
    > .key-combination {
      color: @disabledText-color;
    }
  }
}
</style>
